var Fly = require("flyio/dist/npm/fly")
var fly = new Fly
const wx = require('weixin-js-sdk');

import router from "../router/index";
import { Toast, Dialog } from 'vant';


let host = (function () {
    // 解析url
    var url = window.location.href;
    let pbid = window.localStorage.getItem('pbid')

    
    if((window.location.href).indexOf('localhost') != -1) {
        // 开启本地代理模式
        return `yi/public/index.php?pbid=${pbid}&s=/`
    } else {
        var urlArr = url.split("wap/index.html");
        var reg = new RegExp("pbid=([^&]*)(&|$)");
        var param=urlArr[1].match(reg);
        var get_pbid = String(param[1]).replace('#/','')
        var root_url = urlArr[0]
        console.log('get_pbid',get_pbid)
        console.log('root_url',root_url)
        console.log(`${root_url}index.php?pbid=${get_pbid}&s=/`)
        return `${root_url}index.php?pbid=${get_pbid}&s=/`
    }
})()

// -------------------------------
/** 
 ** 请求封装
 */
function request(url, method, data, header = {}) {
    return new Promise((resolve, reject) => {
        var new_host = host
        Toast.loading();
        if (method == "GET") {

            fly.get(new_host + url)
                .then(function (res) {
                    Toast.clear();
                    console.log(res)
                    resolve(res.data)
                })
                .catch(function (error) {
                    console.log(error)
                })
        } else {
            fly.post(new_host + url, data)
                .then(function (res) {
                    console.log('host:', host)
                    Toast.clear();
                    resolve(res.data)
                })
                .catch(function (error) {
                    console.log(error);
                })
        }
        setTimeout(function () {
        }, 1000)



    })
}

function get(url, data) {
    return request(url, 'GET', data)
}

function post(url, data) {
    return request(url, 'POST', data)
}


// -------------------------------
/** 
 ** 时间戳转换时间
 */

function timeChange(time) {
    var date = new Date(time * 1000);
    var Y = date.getFullYear() + '-';
    var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
    var D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + ' ';
    var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
    var m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
    var s = (date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds());
    return Y + M + D + h + m + s
}


function dateDiff(timestamp) {
    // 补全为13位
    var arrTimestamp = (timestamp + '').split('');
    for (var start = 0; start < 13; start++) {
        if (!arrTimestamp[start]) {
            arrTimestamp[start] = '0';
        }
    }
    timestamp = arrTimestamp.join('') * 1;

    var minute = 1000 * 60;
    var hour = minute * 60;
    var day = hour * 24;
    var halfamonth = day * 15;
    var month = day * 30;
    var now = new Date().getTime();
    var diffValue = now - timestamp;

    // 如果本地时间反而小于变量时间
    if (diffValue < 0) {
        return '不久前';
    }

    // 计算差异时间的量级
    var monthC = diffValue / month;
    var weekC = diffValue / (7 * day);
    var dayC = diffValue / day;
    var hourC = diffValue / hour;
    var minC = diffValue / minute;

    // 数值补0方法
    var zero = function (value) {
        if (value < 10) {
            return '0' + value;
        }
        return value;
    };

    // 使用
    if (monthC > 12) {
        // 超过1年，直接显示年月日
        return (function () {
            var date = new Date(timestamp);
            return date.getFullYear() + '年' + zero(date.getMonth() + 1) + '月' + zero(date.getDate()) + '日';
        })();
    } else if (monthC >= 1) {
        return parseInt(monthC) + "月前";
    } else if (weekC >= 1) {
        return parseInt(weekC) + "周前";
    } else if (dayC >= 1) {
        return parseInt(dayC) + "天前";
    } else if (hourC >= 1) {
        return parseInt(hourC) + "小时前";
    } else if (minC >= 1) {
        return parseInt(minC) + "分钟前";
    }
    return '刚刚';
}



// -------------------------------
/** 
 ** 自用方法
 */
function wxConfig() {
    let selfUrl = window.location.href
    post("shop/api/wx_config", { url: selfUrl })
        .then(res => {

            wx.config({
                debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                appId: res.appId, // 必填，公众号的唯一标识
                timestamp: res.timestamp + "", // 必填，生成签名的时间戳
                nonceStr: res.nonceStr, // 必填，生成签名的随机串
                signature: res.signature,// 必填，签名
                jsApiList: ['chooseWXPay', 'updateAppMessageShareData'] // 必填，需要使用的JS接口列表

            });
        })
        .catch(err => {
            console.log("错误信息：" + err);
        });
}




