// 分享
function goShare() {
    wxConfig()
    let selfUrl = window.location.href
    wx.updateAppMessageShareData({
        title: '123', // 分享标题
        desc: 'asdffdhgsdfg', // 分享描述
        link: selfUrl, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
        imgUrl: '', // 分享图标
        success: function () {
            // 设置成功
        }
    });
}


//  支付
function goPay(id, price) {
    wxConfig();

    get("shop/api/do_pay/out_trade_no/" + id).then(res => {
        if (res.code == 0) {
            Toast(res.msg)
            return false
        }
        // 测试专用
        if (res.code == 2) {
            Toast.success("支付成功");
            setTimeout(function () {
                router.replace({ name: 'done_pay', params: { id: price } });
            }, 1000);
            return false
        }

        wx.chooseWXPay({
            timestamp: res.pay.timeStamp + "",
            nonceStr: res.pay.nonceStr, // 支付签名随机串，不长于 32 位
            package: res.pay.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
            signType: res.pay.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
            paySign: res.pay.paySign, // 支付签名
            success: function (res) {
                router.replace({ name: 'done_pay', params: { id: price } });
            },
            fail: function (e) {
                alert(JSON.stringify(e));
                alert(JSON.stringify(e.err_msg));
            }
        })
    });
}

//  确认收货
function goReceiving(id) {
    Dialog.confirm({
        title: "提示",
        message: "确定收货？"
    })
        .then(() => {
            // on confirm
            get("shop/api/confirm_get?id=" + id).then(res => {
                if (res.code == 1) {
                    Toast.fail("收货成功");
                    router.push({ name: "msg", params: { type: 'success', msg: '收货成功' } });
                } else {
                    Toast.fail("收货失败");
                }
            });
        })
        .catch(() => {
            // on cancel
        });
}



